import React, { Component } from "react";
import { Button } from "antd";
import Core from "../lib/sketchpad.min";
class Sketchpad extends Component {
  isInit = false;

  componentDidMount() {
    setTimeout(this.init, 0);
  }

  init = () => {
    if (this.isInit) {
      return;
    }
    console.log('init')
    this.isInit = true;
    this.core = new Core(this.refs.wrapper);
    this.core.init(this.getParam());
  }

  handleEraser = () => {
    this.core.setTool('eraser');
  }

  handleDrag = () => {
    this.core.setTool('drag');
  }

  handlePen = () => {
    this.core.setTool('pen');
  }

  handleCancel = () => {
    this.core.undo();
  }

  handleClear = () => {
    this.core.clear();
  }

  getParam() {
    return {
      pen: {
        size: 2
      },
      eraser: {
        ico: require('../iamges/eraser.png'),
        w: 38,
        h: 43,
        offsetX: 19,
        offsetY: 21.5,
        type: "rectangle"
      },
      maxStack: 5
    };
  }

  render() {
    const bgStyle = {
      position: "absolute",
      left: 0,
      bottom: 0
    };
    const wrapperStyle = {
      boxShadow: '2px 2px 20px #ccc',
      border: '1px solid #eee',
      background: '#F0F8FF',
      height: "100%"
    }
    return (
      <div
        style={{
          height: "100%",
          position: "relative"
        }}
      >
        <div
          className="sketchpad-wrapper"
          ref="wrapper"
          style={wrapperStyle}
        />
        <Button.Group style={bgStyle}>
          <Button type="primary" onClick={this.handlePen}>
            
          </Button>
          <Button onClick={this.handleEraser}></Button>
          <Button onClick={this.handleDrag}></Button>
          <Button onClick={this.handleCancel}></Button>
          <Button onClick={this.handleClear}></Button>
        </Button.Group>
      </div>
    );
  }
}

export default Sketchpad;
